.conditions {
  background-color: #f6f6f6;
  padding: 100px 0;

  h2 {
    text-transform: uppercase;
    margin-bottom: 50px;
    text-align: center;
  }

  p {
    margin-bottom: 30px;
    font-family: @default-font-light;
    color: #333333;
  }

  li {
    //padding-left: 40px;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  a {
    color: @color-main;
    font-family: @default-font-light;
    position: relative;
    display: inline-block;
    padding-left: 40px;
    text-decoration: underline;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: -5px;
      width: 28px;
      height: 30px;
      background: #fff url(../img/conditions/conditions.png) no-repeat;
      background-size: cover;
    }
  }

}