.leave-request {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  text-align: center;
  padding-bottom: 75px;

  .container {
    @media screen and (max-width: @screen-md) {
      padding: 0 20px;
    }
  }

  h2 {
    text-transform: uppercase;
    color: @color-main;
  }

  &__header-p {
    max-width: 600px;
    margin: 20px auto 50px;
  }

  .form-wrapper {
    position: relative;
    max-width: 630px;
    margin: auto;
    &__bg {
      background-image: linear-gradient( 60deg, rgb(7,220,244) 0%, rgb(0,153,204) 100%);
      opacity: .5;
      position: absolute;
      left: -20px;
      right: -20px;
      top: -20px;
      bottom: -20px;
    }
  }

  form {
    background-color: #fff;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0 10px 20px 0 rgba(0, 153, 175, 0.15);
    position: relative;
    z-index: 2;
    text-align: left;
    label {
      display: block;
      margin-bottom: 15px;
    }
    input {
      width: 570px;
      height: 60px;
      border-radius: 5px;
      border: 1px solid rgb(204, 204, 204);
      padding: 20px;
      font-size: 18px;
      @media screen and (max-width: @screen-md) {
        width: 100%;
      }
      &::placeholder {
        color: #999;
      }
    }
  }

  .form-group {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  .label-color {
    color: @color-main;
  }

  button {
    width: 100%;
    border-radius: 5px;
    background-image: linear-gradient( 0deg, rgb(0,153,204) 0%, rgb(7,220,244) 100%);
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
    color: #fff;
  }

  .label-checkbox-custom {
    display: flex;
    cursor: pointer;
  }

}