.apply {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  text-align: center;
  padding: 100px 0 170px;

  h2 {
    color: @color-main;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  .apply-block {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: @screen-md) {
      flex-wrap: wrap;
      align-items: center;
      width: 330px;
      margin: auto;
    }
    @media screen and (max-width: @screen-sm) {
      width: 100%;
    }
  }

  .apply-item {
    border-radius: 5px;
    //background-image: -moz-linear-gradient( 60deg, rgb(0,153,204) 0%, rgb(7,220,244) 100%);
    border: 2px solid @color-main;
    width: 370px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 45px 10px;
    @media screen and (max-width: @screen-xl) {
      width: 230px;
    }
    @media screen and (max-width: @screen-md) {
      width: 100%;
    }
    &:not(:last-child) {
      @media screen and (max-width: @screen-md) {
        margin-bottom: 20px;
      }
    }
  }

  p {
    margin-top: 15px;
    span {
      cursor: pointer;
      text-decoration: underline;
      color: @color-main;
    }
    a {
      display: block;
      color: @color-main;
      font-family: @default-font-bold;
      font-size: 24px;
    }
  }

}