.header {

  .container {
    display: flex;
    justify-content: space-between;
    margin-bottom: -50px;
    @media screen and (max-width: @screen-md) {
      margin-bottom: 0;
      align-items: center;
      padding: 0;
    }
  }

  &__logo {
    @media screen and (max-width: @screen-md) {
      width: 100px;
    }
  }

  .header-phone {
    margin-top: 25px;
    @media screen and (max-width: @screen-lg) {
      margin-right: 60px;
    }
    @media screen and (max-width: @screen-md) {
      margin-left: auto;
      margin-right: auto;
      margin-top: 0;
    }
    @media screen and (max-width: @screen-sm) {
      margin-left: 15px;
    }
    &__link {
      font-size: 30px;
      font-family: @default-font-bold;
      color: #0099af;
      position: relative;
      padding-left: 35px;
      @media screen and (max-width: @screen-sm) {
        font-size: 18px;
        padding-left: 0;
      }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 5px;
        width: 22px;
        height: 22px;
        background-image: url(../img/header/phone.png);
        background-size: cover;
        background-repeat: no-repeat;
        @media screen and (max-width: @screen-sm) {
          display: none;
        }
      }
    }
  }

}

.main-nav {
  margin-top: 34px;
  .transition();
  @media screen and (max-width: @screen-xl) {
    flex-direction: column;
    position: fixed;
    top: -500px;
    left: 0;
    right: 0;
    width: 100vw;
    margin-top: 0;
    //display: none;
    justify-content: center;
    align-items: center;
  }
  &.active {
    display: flex;
    top: 0;
    // bottom: 0;
    height: 100vh;
    background-color: fade(@color-main, 70%);
  }

  a {
    color: #333333;
    border-bottom: 1px dashed rgb(0, 153, 175);
    @media screen and (max-width: @screen-xl) {
      color: #fff;
      border-color: #fff;
    }
    &:not(:last-child) {
      margin-right: 22px;
      @media screen and (max-width: @screen-xl) {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

}