.about {
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  padding: 100px 0;
  position: relative;
  @media screen and (max-width: @screen-md) {
    padding: 30px 0;
  }
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .about-text {
    max-width: 770px;
    @media screen and (max-width: @screen-md) {
      width: auto;
    }
  }

  h2 {
    text-transform: uppercase;
    margin-bottom: 45px;
    @media screen and (max-width: @screen-md) {
      margin-bottom: 25px;
    }
  }

  p {
    margin-bottom: 25px;
  }

  a {
    text-decoration: underline;
    position: relative;
    padding-left: 35px;
    &:before {
      content: '';
      position: absolute;
      top: -5px;
      left: 0;
      width: 28px;
      height: 30px;
      background-image: url(../img/about/about-download.png);
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  .about-logo {
    @media screen and (max-width: @screen-md) {
      position: absolute;
      right: 0;
      top: 20px;
      opacity: .5;
      width: 140px;
    }
  }

}