.advantages {
  padding: 75px 0;

  .advantages-block {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: @screen-xl) {
      flex-wrap: wrap;
    }
    @media screen and (max-width: @screen-md) {
      justify-content: center;
    }
  }

  .advantages-item {
    width: 270px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 5px;
    background: rgb(255, 255, 255);
    box-shadow: 0px 10px 20px 0px rgba(0, 153, 175, 0.15);
    padding: 30px 20px;
    .transition();
    @media screen and (max-width: @screen-xl) {
      width: 45%;
      margin-bottom: 20px;
    }
    @media screen and (max-width: @screen-md) {
      width: 375px;
    }
    @media screen and (max-width: @screen-sm) {
      width: 100%;
    }
    &:hover {
        background: linear-gradient( 60deg, rgb(0,153,204) 0%, rgb(7,220,244) 100%);
      cursor: pointer;
      p {
        color: #fff;
      }
      .advantages-item__img-first {
        opacity: 0;
      }
      .advantages-item__img-second {
        opacity: 1;
      }
    }
    .img-wrapper {
      position: relative;
      img {
        .transition();
      }
    }
    &__img-second {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
    }
  }

  p {
    font-family: @default-font-light;
    margin-top: 13px;
    .transition();
    span {
      font-family: @default-font-bold;
    }
  }

}