.more-adv {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: #fff;
  text-align: center;
  padding: 100px 0;

  h2 {
    text-transform: uppercase;
  }

  &__header-p {
    font-size: 24px;
    margin-top: 25px;
    margin-bottom: 70px;
  }

  .more-adv-block {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: @screen-md) {
      justify-content: center;
      flex-wrap: wrap;
    }
  }

  .more-adv-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (max-width: @screen-md) {
      width: 45% !important;
    }
    &:nth-child(1) {
      width: 180px;
      @media screen and (max-width: @screen-md) {
        margin-right: 20px;
      }
    }
    &:nth-child(2) {
      width: 204px;
      @media screen and (max-width: @screen-xl) {
        width: 180px;
      }
    }
    &:nth-child(3) {
      width: 140px;
      @media screen and (max-width: @screen-md) {
        margin-right: 20px;
      }
    }
    &:nth-child(4) {
      width: 230px;
      @media screen and (max-width: @screen-xl) {
        width: 200px;
      }
      .img-wrapper {
        padding: 10px 0;
      }
    }
    .img-wrapper {
      margin-bottom: 15px;
      height: 50px;
    }
    p {
      font-family: @default-font-light;
    }
  }

  .btn {
    margin-top: 80px;
  }

}