.footer {
  padding: 30px 0;
  @media screen and (max-width: @screen-xl) {
    text-align: center;
  }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: @screen-xl) {
      flex-direction: column;
      justify-content: center;
    }
  }

  .img-wrapper {
    min-width: 170px;
    @media screen and (max-width: @screen-sm) {
      min-width: 90px;
      width: 90px;
    }
  }

  .footer-rights {
    width: 300px;
    font-family: @default-font-light;
    font-size: 16px;
    @media screen and (max-width: @screen-xl) {
      order: 1;
      width: auto;
    }
    &__year {
      margin-bottom: 20px;
      @media screen and (max-width: @screen-xl) {
        margin-bottom: 10px;
      }
    }
  }

  .footer-contact {
    width: 470px;
    @media screen and (max-width: @screen-xl) {
      margin-top: 10px;
      margin-bottom: 15px;
      width: auto;
    }
    a {
      margin-bottom: 20px;
      color: @color-main;
      font-size: 24px;
      font-family: @default-font-bold;
      display: inline-block;
      @media screen and (max-width: @screen-xl) {
        margin-bottom: 10px;
      }
    }
    p {
      font-family: @default-font-light;
      font-size: 16px;
    }
  }

}