.steps {
  background-color: #f6f6f6;
  padding: 75px 0;

  .steps-block {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: @screen-md) {
      flex-direction: column;
      align-items: center;
    }
  }

  .steps-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 370px;
    position: relative;
    z-index: 1;
    @media screen and (max-width: @screen-xl) {
      width: 230px;
    }
    @media screen and (max-width: @screen-md) {
      width: 400px;
      margin-bottom: 50px;
    }
    @media screen and (max-width: @screen-sm) {
      width: 100%;
    }
    &:before {
      content: '1';
      position: absolute;
      font-family: @default-font-black;
      font-size: 300px;
      line-height: 190px;
      color: #ddedef;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: -1;
      @media screen and (max-width: @screen-md) {
        font-size: 200px;
        line-height: 90px;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    &:after {
      content: '';
      position: absolute;
      right: -25px;
      top: 50%;
      width: 25px;
      height: 25px;
      border-top: 2px solid @color-main;
      border-right: 2px solid @color-main;
      transform: translateY(-50%) rotate(45deg);
      @media screen and (max-width: @screen-md) {
        right: 42%;
        bottom: -30px;
        top: auto;
        transform: translateX(-50%) rotate(135deg);
      }
    }
    &:first-child {
      img {
        margin-top: 10px;
      }
    }
    &:nth-child(2) {
      &:before {
        content: '2';
      }
    }
    &:nth-child(3) {
      &:before {
        content: '3';
      }
      &:after {
        display: none;
      }
    }

    &__title {
      color: @color-main;
      font-size: 20px;
      font-family: @default-font-bold;
      padding-top: 25px;
      padding-bottom: 15px;
    }
    &__descr {
      font-family: @default-font-light;
    }

  }

}