.banner {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 60% 0;
  padding: 234px 0;
  color: #fff;

  .container {
    @media screen and (max-width: @screen-lg) {
      padding: 0 15px;
    }
  }

  h1 {
    text-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  }

  p {
    font-size: 36px;
    max-width: 470px;
    margin-top: 25px;
    margin-bottom: 35px;
    span {
      font-family: @default-font-bold;
    }
  }

}