.map {
  position: relative;

  #map {
    width: 100%;
    height: 760px;
  }

  .map-block {
    position: absolute;
    top: 50px;
    left: 0;
    margin-left: ~"calc(50vw - 600px)";
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 10px 20px 0px rgba(0, 153, 175, 0.15);
    width: 370px;
    max-height: 660px;
    padding: 30px;
    @media screen and (max-width: @screen-xl) {
      margin-left: ~"calc(50vw - 330px)";
    }
    @media screen and (max-width: @screen-md) {
      margin-left: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    @media screen and (max-width: 390px) {
      width: 300px;
    }
    h2 {
      text-transform: uppercase;
      color: @color-main;
      margin-bottom: 22px;
    }
    .form-group {
      margin-bottom: 20px;
    }
    .checkbox-custom {
      margin-top: 3px;
      margin-right: 5px;
    }
    .label-custom {
      font-size: 18px;
    }
  }
  .ui-selectmenu-button.ui-button {
    height: 60px;
    background-color: transparent;
    width: 100%;
  }
  .ui-selectmenu-text,
  .ui-button .ui-icon {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
  .ui-button .ui-icon {
    background-image: url(../img/map/triangle.png);
    background-position: 0 0;
    top: 27px;
  }
  
  .map-content {
    background-color: #f6f6f6;
    margin-top: 15px;
    margin-right: -30px;
    margin-left: -30px;
    //margin-bottom: -30px;
    padding: 30px;
    padding-bottom: 0;
    max-height: 370px;
    overflow: auto;
    font-size: 16px;
    a {
      color: @color-main;
      font-family: @default-font-bold;
    }
    .map-content-item {
      margin-bottom: 20px;
      a {
        margin-bottom: 10px;
      }
    }
  }

}