* {
	box-sizing: border-box;
}
html, body {
	//height: 100%;
}
body {
	font-size: 18px;
	line-height: 1.2;
	position: relative;
	font-style: normal;
	font-family: @default-font;
	overflow-x: hidden;
	opacity: 1;
	background-color: #fff;
	margin: 0;
	padding: 0;
}
.img-wrapper {
	overflow: hidden;
}
img {
	max-width: 100%;
	height: auto;
	display: block; 
}
button {
	outline: none;
	border: none;
}
a {
	text-decoration: none;
	color: #fff;
}
p, ul, h1, h2, h3 {
	padding: 0;
	margin: 0;
}
ul {
	list-style-type: none;
}
.container {
	width: 1170px;
	//padding: 0 10px;
	padding: 0;
	margin: auto;
	@media screen and (max-width: @screen-xl) {
		width: 750px;
	}
	@media screen and (max-width: @screen-md) {
		width: auto;
		padding: 0 10px;
	}
}
h1 {
	font-size: 72px;
	font-family: @default-font-black;
	@media screen and (max-width: @screen-lg) {
		font-size: 48px;
	}
	@media screen and (max-width: @screen-md) {
		font-size: 30px;
	}
}
h2 {
	font-size: 48px;
	font-family: @default-font-light;
	font-weight: normal;
	@media screen and (max-width: @screen-md) {
		font-size: 30px;
	}
}
h3 {
	font-size: 36px;
	font-family: @default-font-light;
}

.btn {
	background-color: #fff;
	color: @color-main;
	font-size: 24px;
	font-family: @default-font;
	border-radius: 5px;
	box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
	cursor: pointer;
	padding: 0 55px;
	display: inline-block;
	line-height: 80px;
	.transition();
	@media screen and (max-width: @screen-sm) {
		font-size: 18px;
		padding: 0 40px;
	}
	&:hover {
		box-shadow: none;
	}
}


/* Скрываем реальный чекбокс */
.checkbox {
	display: none;
}
/* Задаем внешний вид для нашего кастомного чекбокса. Все обязательные свойства прокомментированы, остальные же свойства меняйте по вашему усмотрению */
.checkbox-custom {
	position: relative;      /* Обязательно задаем, чтобы мы могли абсолютным образом позиционировать псевдоэлемент внютри нашего кастомного чекбокса */
	width: 15px;             /* Обязательно задаем ширину */
	height: 15px;            /* Обязательно задаем высоту */
	border: 1px solid #ccc;
	border-radius: 3px;
	min-width: 15px;
	margin-right: 14px;
	margin-top: 2px;
}
/* Кастомный чекбокс и лейбл центрируем по вертикали. Если вам это не требуется, то вы можете убрать свойство vertical-align: middle из данного правила, но свойство display: inline-block обязательно должно быть */
.checkbox-custom,
.label-custom {
	display: inline-block;
	vertical-align: middle;
}
/* Если реальный чекбокс у нас отмечен, то тогда добавляем данный признак и к нашему кастомному чекбоксу  */
.checkbox:checked + .checkbox-custom::before {
	content: "";             /* Добавляем наш псевдоэлемент */
	display: block;			 /* Делаем его блочным элементом */
	position: absolute;      /* Позиционируем его абсолютным образом */
	/* Задаем расстояние от верхней, правой, нижней и левой границы */
	top: 2px;
	right: 2px;
	bottom: 2px;
	left: 2px;
	background: @color-main;     /* Добавляем фон. Если требуется, можете поставить сюда картинку в виде "галочки", которая будет символизировать, что чекбокс отмечен */
	border-radius: 2px;
}
.label-custom {
	font-size: 14px;
	span {
		color: @color-main;
		text-decoration: underline;
		cursor: pointer;
	}
}
.label-checkbox-custom {
	display: flex;
	cursor: pointer;
}

.btn_mnu {
	// visibility: hidden;
	width: 50px;
	height: 50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: transparent;
	cursor: pointer;
	// position: relative;
	position: absolute;
	// left: calc(50% - 25px);
	right: 10px;
	top: 10px;
	z-index: 101;
	display: none;
	@media screen and (max-width: @screen-xl) {
		display: flex;
	}
	@media screen and (max-width: @screen-sm) {
		// top:
	}
}
.btn_row {
	height: 2px;
	width: 60%;
	margin: 2px 0;
	background-color: @color-main;
	position: relative;
	transition: all .5s;
}
.btn_mnu.active .btn_row {
	background-color: #fff;
}
.btn_mnu.active .btn_row:first-child {
	transform: rotate(-135deg);
	top: 6px;
}
.btn_mnu.active .btn_row:last-child {
	transform: rotate(-225deg);
	top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
	opacity: 0;
}

.no-scroll {
	overflow: hidden;
}

.hide {
	display: none;
}


