.channel {
  padding: 100px 0 70px 0;

  h2 {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 45px;
    color: @color-main;
  }

  .channel-block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media screen and (max-width: @screen-xl) {
      justify-content: center;
    }
  }

  .channel-item {
      border-radius: 5px;
      border-width: 1px;
      border-style: solid;
      border-color: @color-main;
      //border-image: gradient(60deg, rgb(0,153,204) 0%, rgb(7,220,244) 100%);
      //background-image: linear-gradient( 60deg, rgb(0,153,204) 0%, rgb(7,220,244) 100%);
      max-width: 570px;
      width: 570px;
      min-height: 160px;
      padding: 0 72px 0 50px;
      margin-bottom: 30px;
      display: flex;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        right: 45px;
        top: 50%;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: cover;
      }
      &:nth-child(1) {
        &:after {
          background-image: url(../img/channel/channel-item-1.png);
          width: 44px;
          height: 70px;
        }
      }
      &:nth-child(2) {
        &:after {
          background-image: url(../img/channel/channel-item-2.png);
          width: 71px;
          height: 53px;
        }
      }
      &:nth-child(3) {
        &:after {
          background-image: url(../img/channel/channel-item-3.png);
          width: 70px;
          height: 70px;
        }
      }
      &:nth-child(4) {
        &:after {
          background-image: url(../img/channel/channel-item-4.png);
          width: 70px;
          height: 68px;
        }
      }
      &__number {
        font-family: @default-font-bold;
        font-size: 36px;
        color: @color-main;
        margin-right: 30px;
      }
  }

}